import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../commons/WebComponentWrapper";
import SortableTree from '@nosferatu500/react-sortable-tree';
import PlaylistAdd from "@mui/icons-material/PlaylistAdd";
import Add from "@mui/icons-material/Add";
import Remove from "@mui/icons-material/Remove";
import Edit from "@mui/icons-material/Edit";
import { EventAPI } from "../../commons/EventAPI";
import { getUniqueElementId } from "../../commons/CommonUtils";
import { klona } from 'klona';

const dispatchedEvents = [EventAPI.SORTABLECONTENTTREE_UPDATED];

/*
    Example: <srs-sortable-contenttree items=[{name: "Node1", label: "Node 1", children: [{name: "Node11", label: "Node 11"}]}, {name: "Node2", label: "Node 2", children: [{name: "Node21", label: "Node 21"}]}]/>
*/

const SrsSortableContentTree = function (props, ref) {
    const [treeData, setTreeData] = useState(props.items);
    const [selectedNodeId, setSelectedNodeId] = useState(null);

    const detectChange = (prevTree, newTree) => {
        return null;
    }

    const updateTree = (newTreeData) => {
        setTreeData((prevTreeData) => {
            let activity = detectChange(prevTreeData, newTreeData);
            if(activity) {
                props.dispatchEvent(EventAPI.SORTABLECONTENTTREE_UPDATED, { payload: { id: props.id || props.elementid, items: treeData, activity } });
            }
            return newTreeData;
        });
    }

    const getMatchingNode = (nodeId, items) => {
        let data = items || treeData;
        if(data) {
            for (const obj of data) {
                if (obj.id === nodeId) {
                    return obj;
                }
                if(obj.children) {
                    const matchingNode = getMatchingNode(nodeId, obj.children);
                    if(matchingNode) {
                        return matchingNode
                    }
                }
            }
        }

        return null;
    }

    function getParentNode(targetId, items) {
        let data = items || treeData;
        let parentNode = null;
      
        function findParentNode(nodes) {
          for (let i = 0; i < nodes.length; i++) {
            const node = nodes[i];
            
            if (node.children) {
              for (let j = 0; j < node.children.length; j++) {
                const childNode = node.children[j];
                if (childNode.id === targetId) {
                  parentNode = node;
                  return;
                }
              }
              
              findParentNode(node.children);
            }
          }
        }
      
        findParentNode(data);
        return parentNode;
    }      

    const generateNodeProps = ({ node, path }) => {
        return {
            buttons: [
                <div style={{width: "30px"}}>
                    <Edit sx={{ width: "30px" }}
                        onClick={() => {
                            console.log("Edit", node, path);
                            props.dispatchEvent(EventAPI.SORTABLECONTENTTREE_EDITNODE, { payload: { id: props.id || props.elementid, nodeId: node.id } });
                        }}>
                        Edit
                    </Edit>
                </div>,
                <div style={{width: "30px"}}>
                    <Add sx={{ width: "30px" }}
                        onClick={() => {
                            console.log("Add", node, path);
                            let updatedTreeData = klona(treeData);
                            let parentNode = getParentNode(node.id, updatedTreeData);
                            if(parentNode) {
                                parentNode.children = parentNode.children || [];
                                parentNode.children.push({ name: "Empty Slot", label: "Empty Slot", id: getUniqueElementId(), children: [] });
                            }else {
                                updatedTreeData.push({ name: "Empty Slot", label: "Empty Slot", id: getUniqueElementId(), children: [] });
                            }
                            updateTree(updatedTreeData);
                        }}>
                        Add
                    </Add>
                </div>,
                <div style={{width: "30px"}}>
                    <PlaylistAdd sx={{ width: "30px" }}
                        onClick={() => {
                            console.log("Add child", node, path);
                            let updatedTreeData = klona(treeData);
                            let currentNode = getMatchingNode(node.id, updatedTreeData);
                            currentNode.children = node.children || [];
                            currentNode.children.push({ name: "Empty Slot", label: "Empty Slot", id: getUniqueElementId(), children: [] });
                            updateTree(updatedTreeData);
                        }}>
                        Add
                    </PlaylistAdd>
                </div>,
                <div style={{width: "30px"}}>
                    <Remove sx={{ width: "30px" }}
                        onClick={() => {
                            console.log("Remove", node, path);
                            let updatedTreeData = klona(treeData);
                            let parentNode = getParentNode(node.id, updatedTreeData);
                            if(parentNode) {
                                parentNode.children = parentNode.children.filter((child) => child.id != node.id);
                            }else {
                                if(updatedTreeData && updatedTreeData.children) {
                                    updatedTreeData.children = updatedTreeData.children.filter((child) => child.id != node.id);
                                }
                            }
                            updateTree(updatedTreeData);
                        }}>
                        Remove
                    </Remove>
                </div>
            ]
        }
    };

    useEffect(() => {
        setTreeData(props.items);
    }, [props.items]);

    return (<div style={{ height: props.height }}>
        <SortableTree
            treeData={treeData}
            onChange={data => updateTree(data)}
            generateNodeProps={generateNodeProps}
        />
    </div>);
}

